/**
 * Grid
 */

.row,
.row--full {
	@extend %row;
}

.row {
	max-width: $max-width;
}

.col {
	@include alternative-col();
}

@each $point, $point_size in $grid-breakpoints {
	@include breakpoint-up($point) {
		.#{$point}\: {
			&col {
				@include alternative-col();
			}
		}
	}
}